.skills-info-card{
    min-height: 23rem;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    background: rgba(22,17,47,0.398);
    backdrop-filter: blur(1rem);
}

.skills-info-card h6{
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.8rem 2rem;
    background: linear-gradient(180deg,#ac96ff 0%,#7c5fe6 100%);
    background-clip: text;
    border-radius: 1.5px solid #67519b;
}

.skills-info-content{
    padding: 2rem;
    width: 30rem;
}

.skills-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.skills-info p{
    font-size: 1rem;
    font-weight: 500;
}

.skills-info .percentage{
    color:#dd8cfa ;
}

.skill-progress-bg{
        width: 100%;
        height: 0.5rem;
        background: #382e68;
        border-radius: 0.5rem;
        margin: 1rem 0;
        overflow: hidden;
}

.skill-progress{
    width: 0%;
    height: 0.5rem;
    background:linear-gradient(90deg,#aa94fe -50%,#7d60e6 100%);
    border-radius: 0.5rem;
    transition: all 0.5s ease-in-out;
}